import "./Footer.scss";
import { useMainContext } from "../../Context/MainContext";
import { Link } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import { data } from "../../components/Data/QuranReaders"

const Footer = () => {
  const { theme, setSurahNumber ,setReaderName, setReaderImg, setReaderInfo,} = useMainContext();
  const navigate = useNavigate();
 
  const handleNavigate = (path) => {
    window.scrollTo({
      top: 0,
      left: 0,
      behavior: "smooth"
    })
    navigate(path)
  }

  const handleReaderNavigate = (name)=> {
     data.map((item) => {
      if(item.title === name){
        setReaderImg(item.img)
        setReaderInfo(item.info)
        setReaderName(item.title)
      }
    })
    window.scrollTo({
      top: 0,
      left: 0,
      behavior: "smooth"
    })
    navigate("/reader")
    
  }
 
  return (
    <footer className={theme}>
      <div className="footer_content container ">
        <div className="footer_text">
      <div className="footer_header">
      <h2>   </h2>
          <h3>     . </h3>
      </div>
          <p>
             .   .       
              .       
                 
          </p>
        </div>
        <div className="footer_links">
          <ul>
            <li>
              <h2>   </h2>
            </li>
            <li>
              <Link to='/read-ayah' onClick={()=> window.scrollTo({top: 0, left: 0, behavior: "smooth"})} >   </Link>
            </li>
            <li>
              <Link to='/read-surah' onClick={()=> setSurahNumber(36)} >   </Link>
            </li>
            <li>
              <Link to='/read-surah' onClick={()=> setSurahNumber(67)} >   </Link>
            </li>
            <li>
              <Link to='/read-surah' onClick={()=> setSurahNumber(55)} >   </Link>
            </li>
            <li>
              <Link to='/read-surah' onClick={()=> setSurahNumber(18)} >   </Link>
            </li>
            <li>
              <Link to='/read-surah' onClick={()=> setSurahNumber(73)} >   </Link>
            </li>
            <li>
              <Link to='/read-surah' onClick={()=> setSurahNumber(56)} >   </Link>
            </li>
          </ul>
         <ul>
          <li> <h2>  </h2> </li>
          <li> <Link to="/reader" onClick={()=> handleReaderNavigate("  ")} >    </Link> </li>
          <li> <Link to="/reader" onClick={()=> handleReaderNavigate(" ")} >   </Link> </li>
          <li> <Link to="/reader" onClick={()=> handleReaderNavigate("  ")} >    </Link> </li>
          <li> <Link to="/reader" onClick={()=> handleReaderNavigate("  ")} >    </Link> </li>
          <li> <Link to="/reader" onClick={()=> handleReaderNavigate("  ")} >    </Link> </li>
          <li> <Link to="/reader" onClick={()=> handleReaderNavigate(" ")} >   </Link> </li>
         </ul>
          <ul>
            <li> <h2>  </h2>  </li>
            <li onClick={()=> handleNavigate("/")} >  <Link  >   </Link> </li>
            <li onClick={()=> handleNavigate("/radio")} > <Link>   </Link> </li>
            <li onClick={()=> handleNavigate("/AllReader")} > <Link>   </Link> </li>
            <li onClick={()=> handleNavigate("/boukhary")} > <Link>    </Link> </li>
            <li onClick={()=> handleNavigate("/muslim")} > <Link>    </Link> </li>
            <li onClick={()=> handleNavigate("/azkar")} > <Link>   </Link> </li>
          </ul>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
