import "./Surah.scss";
import { useMainContext } from "../../Context/MainContext";
import { useEffect, useState } from "react";
import axios from "axios";
import Loading from "../../components/Loading/Loading";
import Progress from "../../components/Progress/Progress";

const Surah = () => {
  const { theme, surahNumber, setSurahNumber, fontSize, fontFamily } =
    useMainContext();
  const [surahText, setSuraText] = useState(null);
  const [surahInfo, setSuraInfo] = useState(null);
  const [windoeSize, setWindowSize] = useState(window.innerWidth);
  const [scrollHeight, setScrollHeight] = useState(null)
 
  let surahFontSize;

  useEffect(() => {
    const handleResize = () => setWindowSize(window.innerWidth);

    window.addEventListener("resize", handleResize);

    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, [window.innerWidth]);


  useEffect(()=> {
    const height = document.documentElement.scrollHeight - document.documentElement.clientHeight;
    
    const handleProgress = ()=> {
        const scrollTop = document.documentElement.scrollTop;
        setScrollHeight((scrollTop / height) * 100)
    }

    window.addEventListener("scroll", handleProgress)

    return () => {
      window.removeEventListener("scroll", handleProgress)
    }

  }, [document.documentElement.scrollTop])

  if (windoeSize <= 768) {
    surahFontSize =
      fontSize === 1
        ? 17
        : fontSize === 2
        ? 19
        : fontSize === 3
        ? 21
        : fontSize === 4
        ? 23
        : fontSize === 5
        ? 25
        : fontSize === 6
        ? 27
        : 29;
  } else {
    surahFontSize =
      fontSize === 1
        ? 20
        : fontSize === 2
        ? 22
        : fontSize === 3
        ? 25
        : fontSize === 4
        ? 28
        : fontSize === 5
        ? 30
        : fontSize === 6
        ? 33
        : 35;
  }

  const handleClick = () => {
    setSurahNumber((x) => x + 1);
    window.scrollTo({
      top: 0,
      left: 0,
      behavior: "smooth",
    });
  };


 window.addEventListener("scroll", (e)=> {

 })

  useEffect(() => {
    const fetchSurahData = async () => {
      try {
        const response = await axios.get(
          `https://api.alquran.cloud/v1/surah/${surahNumber}`
        );
        setSuraText(response.data.data.ayahs);
        setSuraInfo(response.data.data);
        window.scrollTo({
          top: 0,
          left: 0,
          behavior: "smooth",
        });
      } catch (error) {
        console.error("Error in fetchin data", error);
      }
    };
    fetchSurahData();
  }, [surahNumber]);

  return (
    <div className={`surah_raed ${theme}`}>
      <Progress scrollHeight= {scrollHeight} />
      <div className="container">
        <div className="surah_info">
          {surahInfo && (
            <>
              <h2> {surahInfo.name} </h2>
              <h3>     </h3>
            </>
          )}
        </div>
        <div className="all_ayahs">
          {surahText ? (
            surahText.map((ayah) => (
              <div className="surah_text" key={ayah.number}>
                <span
                  style={{
                    fontSize: `${surahFontSize}px`,
                    fontFamily: `${fontFamily}`,
                  }}
                >
                  {ayah.text.split("   ")}
                </span>
                <span className={surahFontSize >= 25 ? "number" : "number mini"}>
                  {ayah.numberInSurah}
                </span>
              </div>
            ))
          ) : (
            <Loading />
          )}
        </div>
        <div className="btn" onClick={() => handleClick()}>
           
        </div>
      </div>
    </div>
  );
};

export default Surah;
