import React, { useEffect, useRef } from "react";

const Azkar = ({ data, nameOfAzkar }) => {

  const elementRef =  useRef()

  useEffect(()=>{
    const rect = elementRef.current.getBoundingClientRect()
    console.log(rect)
    window.scrollTo({
      top: rect.top,
      left: 0,
      behavior: "smooth"
    })
  }, [elementRef])

  return (
    <div className="azkar container">
      <h1 ref={elementRef} > {nameOfAzkar} </h1>
      {data.map((item) =>
        item.data ? (
          <div className="zekr" key={item.id}>
            <h1 className="name">{item.title}</h1>
            {item.data.map((item) => (
              <h2 className="doaaa" > <span> ** </span> {item} </h2>
            ))}
          </div>
        ) : (
          <div className="zekr" key={item.id}>
            {item.title && <p> {item.title} </p>}
            {item.name && <h1 className="name"> {item.name} </h1>}
            <h2 key={item.id} > {item.text} </h2>
            {item.count && ( <p> <span>   </span> {item.count} </p>)}
            {item.where && <p> {item.where} </p>}
            {item.info && <h4> {item.info} </h4>}
          </div>
        )
      )}
    </div>
  );
};

export default Azkar;
