
import { useMainContext } from "../../Context/MainContext";
import { useNavigate } from "react-router-dom";
const Jus = () => {
  const { theme,setJushNumber } = useMainContext();
  const navigate = useNavigate()

  const juzsInfo = [
    { title: " ", id: 1 },
    { title: " ", id: 2 },
    { title: " ", id: 3 },
    { title: " ", id: 4 },
    { title: " ", id: 5 },
    { title: " ", id: 6 },
    { title: " ", id: 7 },
    { title: " ", id: 8 },
    { title: " ", id: 9 },
    { title: " ", id: 10 },
    { title: "  ", id: 11 },
    { title: "  ", id: 12 },
    { title: "  ", id: 13 },
    { title: "  ", id: 14 },
    { title: "  ", id: 15 },
    { title: "  ", id: 16 },
    { title: "  ", id: 17 },
    { title: "  ", id: 18 },
    { title: "  ", id: 19 },
    { title: " ", id: 20 },
    { title: "   ", id: 21 },
    { title: "   ", id: 22 },
    { title: "   ", id: 23 },
    { title: "   ", id: 24 },
    { title: "   ", id: 25 },
    { title: "   ", id: 26 },
    { title: "   ", id: 27 },
    { title: "   ", id: 28 },
    { title: "   ", id: 29 },
    { title: " ", id: 30 },
  ];

  const handleClick = (number)=>{
    setJushNumber(number)
    navigate('/surahs-of-jus');
  }

  return (
    <>
      <div className={"jus_all " + theme}>
        <div className="container">
          <div className="jus_all_content">
            {juzsInfo.map((item) => (
              <div className="jus" key={item.id} onClick={()=>handleClick(item.id)} >
                <div className="jus_name"> {item.title} </div>
                <div className="jus_number"> <span> {item.id} </span> </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </>
  );
};

export default Jus;
